const $ = require('jquery');

module.exports = function () {
    const playBtn = $('<a></a>')
        .html('&#xe602;')
        .addClass('controls js-playbtn play');

    playBtn.on('click', function () {
        let $this = $(this);

        if ($this.hasClass('play')) {
            $this.html('&#xe601;');
            $this.removeClass('play').addClass('pause');
            $('audio').get(0).play();
        } else {
            $this.html('&#xe602;');
            $this.removeClass('pause').addClass('play');
            $('audio').get(0).pause();
        }
    })
    return playBtn;
}


