const $ = require('jquery');
const Songs = require('../Songs');

module.exports = function () {
    const listWrapper = $('<div></div>')
            .css({
                position: 'fixed',
                left: 0,
                bottom: 0,
                zIndex: 999,
                display: 'flex',
                flexDirection: 'column',
                justifyContent: 'flex-end',
                width: '100%',
                height: '100%',
                backgroundColor: 'transparent',
                overflow: 'hidden'
            })
            .addClass('js-listwrapper');
    
    // listContent 
    const listContent = $('<div></div>')
            .css({
                position: 'relative',
                width: '100%',
                height: '80%',
                backgroundColor: 'rgba(255,255,255,.9)',
                overflow: 'hidden'
            })
            .addClass('js-listContent');

    // 
    const backBtn = $('<button></button>').text('')
            .css({
                position: 'absolute',
                top: 0,
                left: 0,
                width: '100%',
                height: '10%',
                fontSize: '.2rem',
            })
            .addClass('js-backbtn');
    
    backBtn.on('click', function () {
        if ($(this).data('onOff')) return;

        $('.js-backbtn').data('iscrollwrapper').width('100%');
        
        $('.js-backbtn').data('currentwrapper').animate({
            width: 0
        }, 'fast');

        // div  div
        $('.js-backbtn').data('currentwrapper',
            $('.js-backbtn').data('iscrollwrapper')
         )
        
        $('.js-backbtn').data('iscrollwrapper',
            $('.js-backbtn').data('currentwrapper').data('father')
        )

        //  .js-0
        // btn
        if ($(this).data('currentwrapper').hasClass('js-0')) {
            $(this).text('');
            $(this).data('onOff',true);
        }
    })

    listContent.append(backBtn);
    
    /*
    * Songs: JSON
    * hook:   div.iscrollWrapper 
    * pwd: 
    */
    let hook = 0;
    function checkSongs(Songs, parentNode, pwd, oDiv) {
        // 
        if ( !(Songs instanceof Array) ) {

            //  div  ul  iscroll  Wrapper
            let div = $('<div></div>').addClass('iscrollWrapper')
                    .addClass('js-'+hook);
            let ul = $('<ul></ul>');
            ul.appendTo(div);
            div.appendTo(parentNode);
            div.data('father', oDiv);
            //   ul
            for (let x in Songs) {
                let li = $('<li></li>').text(x);
                li.appendTo(ul);

                li.data('hook',++hook);

                //  li  div.iscrollWrapper
                li.on('click', function () {
                    $('.iscrollWrapper').width(0);
                    
                    $('.js-'+li.data('hook'))
                            .animate({
                                width: '100%'
                            }, 'fast');

                    if (li.data('hook') !== 0) {
                        // 
                        $('.js-backbtn').data('onOff', false);
                        $('.js-backbtn').text('');
                    }
                    
                    //  li   .iscrollwrapper  .backbtn  data
                    $('.js-backbtn').data('iscrollwrapper', li.parent().parent())
                                    .data('currentwrapper', $('.js-'+ li.data('hook')));


                    return false;
                });

                // 
                checkSongs(Songs[x], listContent, pwd + x + '/', div);
            }
        } else {

            let div = $('<div></div>').addClass('iscrollWrapper')
                    .addClass('js-'+hook);
            let ul = $('<ul></ul>');
            ul.appendTo(div);
            div.appendTo(parentNode);
            div.data('father', oDiv);
            for (let x of Songs) {
                let li = $('<li></li>').text(x).addClass('no-after');
                li.appendTo(ul);
            }

            //  li  div.iscrollWrapper 
            div.on('click', 'li', function () {

                //  
                window.currentMune = [];
                $(this).parent().find('li')
                        .each(function (i, elem) {
                            window.currentMune.push(
                                    pwd + elem.innerHTML)
                        });
                
                // 
                window.currentSong = pwd + this.innerHTML;

                $('source')[0].src = pwd + this.innerHTML + '.mp3';
                $('source')[1].src = pwd + this.innerHTML + '.ogg';

                //  js-played  0:00
                $('.js-played').text('0:00');

                //  loading
                $('.wrapper').removeClass('hide');

                // title
                $('h2').text(this.innerHTML);

                //  .playing
                $('.no-after').removeClass('playing');
                $(this).addClass('playing');
                
                //  playerBtn 
                if ($('.js-playbtn').hasClass('pause')) {
                    $('.js-playbtn').triggerHandler('click');
                }

                clearInterval(window.progressLoadBarTimer);
                $('.js-progressLoadBar').width(0);
                $('.js-progressPlayedBar').width(0);

                $('audio')[0].load();

                //  listWrapper
                $('.js-listwrapper').animate({
                    height: 0
                }, 'fast');
            });
        }
    }
    

    checkSongs(Songs, listContent, window.publicURL)

    const closeBtn = $('<div></div>')
            .css({
                width: '100%',
                backgroundColor: 'rgba(255,255,255,.9)',
                boxSizing: 'border-box',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                height: '10%',
                fontSize: '0.25rem',
                borderTop: '1px solid #ddd',
                alignSelf: 'flex-end'
            })
            .addClass('js-close')
            .text('');

    // listContent.append(listUl);
    
    listWrapper.append(listContent)
    .append(closeBtn);


    //  wrapper 
    listWrapper.on('click', function (e) {

        let event = $(e.target);
        let closeOnOff = event.hasClass('js-listwrapper') || event.hasClass('js-close');

        //  listWrapper
        if (closeOnOff) {
            $(this).animate({
                height: 0,
            }, 'fast')
        }

    })

    return listWrapper;
}


