const $ = require('jquery');

// let html = `<div class="wrapper">
//   <div class="bouncing_ball">
//     <div class="ball"></div>
//   </div>
//   <h1 class="loading"><span class="dot_one"> .</span><span class="dot_two"> .</span><span class="dot_three"> .</span></h1>
// </div>`;

module.exports = $('<div></div>').addClass('wrapper')
    .append(
        $('<div></div>').addClass('bouncing_ball')
            .append(
                $('<div></div>').addClass('ball')
            )
    )
    .append(
        $('<h1></h1>').addClass('loading')
            .html('<span class="dot_one"> .</span><span class="dot_two"> .</span><span class="dot_three"> .</span>')
    );


