const $ = require('jquery');

module.exports = function () {
    const loopBtn = $('<a></a>');

    loopBtn.addClass('controls js-loopbtn');

    loopBtn.html('&#xe60a;').addClass('repeat-all');
    window.loopType = 'repeat-all';
    // 
    loopBtn.on('click', function () {
        
       if ($(this).hasClass('repeat-all')) {
           $(this).removeClass('repeat-all').addClass('repeat-one');
           $(this).html('&#xe60b;');
           window.loopType = 'repeat-one';
           return;
       }

       if ($(this).hasClass('repeat-one')) {
           $(this).removeClass('repeat-one').addClass('repeat-all');
           $(this).html('&#xe60a;')
           window.loopType = 'repeat-all';
           return;
       }

    })

    return loopBtn;
}


