const $ = require('jquery');
const ChangeProgressLoadBar = require('./method/ChangeProgressLoadBar');
const Songs = require('../Songs.js');

module.exports = function (obj) {
    const audio = $('<audio></audio>')
        .attr({
            autoplay: 'true'
        });
    const src1 = $('<source>')
        .attr({
            src: window.publicURL + '/' + '/' + Songs[''][''][0]+'.mp3',
            type:'audio/mp3'
        });
    const src2 = $('<source>')
        .attr({
            src: window.publicURL + '/' + '/' + Songs[''][''][0]+'.ogg',
            type: 'audio/ogg'
        });

    audio.append(src1).append(src2);

    // audio  
    audio.on('playing', function () {
        $('.js-playbtn').html('&#xe601;').removeClass('play').addClass('pause');
    })
    audio.on('durationchange', function () {
        var h = Math.floor(this.duration / 3600);
        var m = Math.floor(this.duration % 3600 / 60);
        var s = Math.floor(this.duration % 60);
        h = h > 0 ? h+'' : '';
        if (m > 9) {
            m = m + '';
        } else {
            m = '0' + m;
        }
        if ( s > 9) {
            s = s + '';
        } else{
            s = '0' + s;
        }
        let timeString = h ? (h + ':' + m + ':' + s) : (m + ':' + s);
        $('.js-duration').text(timeString);

        // 
        ChangeProgressLoadBar(this);

        // 
        // $('audio')[0].play();

        // loading 
        $('.wrapper').addClass('hide');
    });

    audio.on('progress', function () {
        
    });

    audio.on('timeupdate', function () {
        let audio = $('audio').get(0);
        let end = audio.buffered.end(audio.buffered.length -1);
        let now = audio.currentTime;

        //  loading 
        if (now >= end) {
            $('.wrapper').removeClass('hide');
        } else {
            $('.wrapper').addClass('hide');
        }
        
        if ($('.js-progressPlayedBar').data('change')) {
            let length = $('.js-progressBarContainer').width();
            let width = this.currentTime / this.duration * length;
            $('.js-progressPlayedBar')
                .width(width);
        }
        
        let playedTime = $('.js-played');
        let currentTime = this.currentTime;
        let h = Math.floor( currentTime / 3600 );
        let m = Math.floor( currentTime % 3600 / 60);
        let s = Math.floor( currentTime % 60 );
        let timeString = '';
        if ( h > 0) {
            h = h > 9 ? '' + h : '0' + h;
            m = m > 9 ? '' + m : '0' + m;
            s = s > 9 ? '' + s : '0' + s;
            timeString = [h,m,s].join(':');
        } else {
            m = m > 9 ? '' + m : '0' + m;
            s = s > 9 ? '' + s : '0' + s;
            timeString = [m,s].join(':');
        }
        playedTime.text(timeString);
    });

    audio.on('ended', function () {
        if ($('.js-progressPlayedBar').data('change')) {

            if ( window.loopType === 'repeat-all' ) {
                $('.js-nextbtn').triggerHandler('click');
            }

            if ( window.loopType === 'repeat-one' ) {
                $('audio')[0].load();
            }

        }
    });

    return audio;
}


