const $ = require('jquery');

function progressLoading (audio) {
    const progress = $('.js-progressLoadBar');
    let duration = audio.duration;

    clearInterval(window.progressLoadBarTimer);
    window.progressLoadBarTimer = setInterval(function () {
        let length = progress.parent().width();
        progress.width(audio.buffered.end(0) / audio.duration * length);
        if(audio.buffered.end(0) === audio.duration) {
            progress.width('100%');
            clearInterval(window.progressLoadBarTimer)
        }
    }, 1000);
}

module.exports = progressLoading;


