var $ = require('jquery');

module.exports = function (argv) {
    let header = $('<header></header>');
    let h2 = $('<h2></h2>').text(argv.title);
    header.append(h2);
    header.css(argv.css);
    h2.css(argv.h2_css);

    return header;
}


