require('../style/main.scss');
const $ = require('jquery');
const CreatePlayerTitle = require('./component/CreatePlayerTitle');
const CreateControls = require('./component/CreateControls');
const CreateProgress = require('./component/CreateProgress');
const CreateAudio = require('./component/CreateAudio');
const CreateList = require('./component/CreateList');
const weChatHacks = require('./wechatHacks');
const Songs = require('./Songs');
const IScroll = require('iscroll');
const Loading = require('./component/CreateLoading');

// 
$(function () {
    weChatHacks();
});

window.publicURL = 'http://ouk2f6s3b.bkt.clouddn.com/';

const header = new CreatePlayerTitle({
    title: Songs[window.currentSong] || '',
    css: {
        display: 'flex',
        alignItems: 'center',
        height: '10%',
        background: 'rgba(255,255,255,.3)',
        padding: '1px',
        textAlign: 'center',
        borderBottom: '1px solid rgba(255,255,255,.5)',
        boxShadow: '0 0 10px rgba(255,255,255,.6)'
    },
    h2_css: {
        margin: '0 auto',
        color: '#eee',
        textShadow: '0 0 10px rgba(0,0,0,.1)',
        fontWeight: 'normal',
        fontSize: '.24rem'
    }
});
const progress = new CreateProgress({
    audio: audio,
    progress: progress
});
const controls = new CreateControls({
    audio: audio,
    progress: progress
});
const audio = new CreateAudio({
    playBtn: controls.playBtn
});
const list = new CreateList();

$(document).ready(function () {
    $('#app')
        .append(header)
        .append(progress)
        .append(Loading)
        .append(controls.controls)
        .append(list)
        .append(audio);
    
    //  iscroll
    $('.iscrollWrapper').each(function ( i, elem ) {
        let myscroll = new IScroll(elem, {
            mouseWheel: true,
            click: true
        })
    });
    /*  */
    //  .js-listwrapper  height  0
    //  iScroll 
    $('.js-listwrapper').height(0);
    /*  */

    //  li.no-after  .playing
    $('.no-after')[0].classList.add('playing');
    
     //  h2 
     $('h2').text(Songs[''][''][0]);
});


