const $ = require('jquery');

module.exports = function () {
    const listBtn = $('<a></a>');

    listBtn.addClass('controls');

    listBtn.html('&#xe60c;');

    listBtn.on('click', function () {
        $('.js-listwrapper').animate({
            height: '100%'
        }, 'fast');
    })

    return listBtn;
}


