const $ = require('jquery');

module.exports = function () {
    const nextBtn = $('<a></a>')
        .html('&#xe608;')
        .addClass('controls js-prevbtn');

    // 
    nextBtn.on('click', function () {

        // window.currentMenu  window.currentSong
        if ( !window.currentMenu || !window.currentSong ) {
            let prefix = decodeURI($('source')[0].src);
            let currentSongName = $('.playing').text();
            prefix = prefix.replace(currentSongName+'.mp3', '')
            prefix = prefix.replace(document.URL, './')

            //  window.currentMenu
            window.currentMenu = [];
            $('.playing').parent().find('li')
                    .each(function (i, elem) {
                        window.currentMenu.push(prefix + elem.innerHTML);
                    });

            //  window.currentSong
            window.currentSong = prefix + $('.playing').text();
        }

        //  loading
        $('.wrapper').removeClass('hide');

        //  window.currentSong  window.currentMenu 

        let num = window.currentMenu.indexOf(window.currentSong);
        
        --num;
        num = num < 0 ? $('.playing').parent().find('li').length - 1 : num;

        //  window.currentSong
        window.currentSong = window.currentMenu[num];
        //  playing
        let parent = $('.playing').parent();
        $('.playing').removeClass('playing');
        parent.find('li').eq(num).addClass('playing');

        // 
        $('h2').text( $('.playing').parent().find('li').eq(num).text() );
       
       

        $('source:nth-of-type(1)')[0].src = window.currentMenu[num] + '.mp3';
        $('source:nth-of-type(2)')[0].src = window.currentMenu[num] + '.ogg';

        // $('h2').text(window.currentMenu[num]);

        if ($('.js-playbtn').hasClass('pause')) {
            $('.js-playbtn').triggerHandler('click');
        }

        //  .js-played  0:00
        $('.js-played').text('0:00');

        clearInterval(window.progressLoadBarTimer);
        $('.js-progressLoadBar').width(0);
        $('.js-progressPlayedBar').width(0);

        $('audio')[0].load();
   });

    return nextBtn;
}


