const $ = require('jquery');
const CreateLoopBtn = require('./controlBtns/CreateLoopBtn');
const CreatePrevBtn = require('./controlBtns/CreatePrevBtn');
const CreatePlayBtn = require('./controlBtns/CreatePlayBtn');
const CreateNextBtn = require('./controlBtns/CreateNextBtn');
const CreateListBtn = require('./controlBtns/CreateListBtn');

module.exports = function (obj) {
    const ctrlWrapper = $('<div></div>')
        .css({
            position: 'fixed',
            bottom: '0',
            left: '0',
            width: '100%',
            height: '20%',
            display: 'flex',
            boxSizing: 'border-box',
            justifyContent: 'space-between',
            alignItems: 'center',
            padding: '0 .2rem'
        });

    const loopBtn = new CreateLoopBtn();
    const prevBtn = new CreatePrevBtn();
    const playBtn = new CreatePlayBtn();
    const nextBtn = new CreateNextBtn();
    const listBtn = new CreateListBtn();
    loopBtn.appendTo(ctrlWrapper);
    prevBtn.appendTo(ctrlWrapper);
    playBtn.appendTo(ctrlWrapper);
    nextBtn.appendTo(ctrlWrapper);
    listBtn.appendTo(ctrlWrapper);
    
    return {
        controls: ctrlWrapper,
        playBtn : playBtn
    };
};


