const $ = require('jquery');

module.exports = function () {
    const progressWrapter = $('<div></div>')
        .css({
            position: 'fixed',
            bottom: '20%',
            left: 0,
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'space-around',
            width: '100%',
            fontSize: '.2rem',
            color: '#fff'
        })
        .addClass('js-progressWrapper');

    const playedTime =  $('<span></span>')
        .text('0:00')
        .css({
            fontSize: '.16rem',
            display: 'block'
        })
        .addClass('js-played');
    const allTime = $('<span></span>')
        .text('0:00')
        .css({
            fontSize: '.16rem',
            display: 'block'
        })
        .addClass('js-duration');

    const progressBarContainer = $('<div></div>')
        .css({
            width: '64%',
            height: '4px',
            borderRadius: '2px',
            backgroundColor: 'rgba(255,255,255,.5)'
        })
        .addClass('js-progressBarContainer');
    const progressBarLoadBar = $('<div></div>')
        .css({
            width: 0,
            backgroundColor: 'rgba(0,0,0, .2)',
            borderRadius: '2px',
            height: '100%'
        })
        .addClass('js-progressLoadBar');
    const progressPlayed = $('<div></div>')
        .css({
            position: 'relative',
            width: 0,
            background: 'yellow',
            borderRadius: '2px',
            height: '100%'
        })
        .addClass('js-progressPlayedBar')
        .data('change', true);
    const playerBtn = $('<div></div>')
        .css({
            position: 'absolute',
            top: '-6px',
            right: 0,
            width: '15px',
            height: '15px',
            border: '2px solid #fff',
            borderRadius: '50%',
            background:'#fff',
            transition: '.2s ease-out'
        })
        .addClass('timeBtn')
        .appendTo(progressPlayed);
    // 
    playerBtn.on('touchstart', function () {
        $('.js-progressPlayedBar').data('change', false);
        let $this = $(this);
        $this.css({
            transform: 'scale(1.5,1.5)'
        });
        return false;
    });
    playerBtn.on('touchend', function () {
        let $this = $(this);
        $this.css({
            transform: 'scale(1,1)'
        });

        $('.wrapper').addClass('hide');

        //  .js-progressPlayedBar  true
        $('.js-progressPlayedBar').data('change', true);
        //  audio ended  touchmove 
        //   
        if ($('audio')[0].currentTime === $('audio')[0].duration) {
            $('audio').triggerHandler('ended');
        }
        return false;
    });
    
    playerBtn.on('touchmove', function (event) {
        //  progressPlayed 
        // 
        progressPlayed.data('change', false);

        //   X 
        let parentRect = progressBarContainer[0].getBoundingClientRect();
        let left = parentRect.left;

        let currentX = event.originalEvent.targetTouches[0].clientX;

        let minWidth = 0;
        let maxWidth = parentRect.width;
        
        let width = currentX - left;

        if (width < minWidth) width = minWidth;
        if (width > maxWidth) width = maxWidth;
        
        //  progressPlayed 
        progressPlayed.width(width);

        //  playedTime 
        let divisor = width / maxWidth;
        let currentTime = $('audio')[0].duration * divisor;
        $('audio')[0].currentTime = currentTime;
    });
    
    progressBarLoadBar
        .appendTo(progressBarContainer)
        .append(progressPlayed);

    progressWrapter
        .append(playedTime)
        .append(progressBarContainer)
        .append(allTime);

    // 
    $(window).on('orientationchange', function () {
        // X5
        // 
        // progressBarLoadBar
        // 
        setTimeout(function () {
            progressBarLoadBar.width(function () {
                let width = progressBarContainer.width();
                let audio = $('audio')[0];
                let n = audio.buffered.end(0) / audio.duration * width;
                return n;
            })
        }, 150);
    })

    return progressWrapter;
};


